.masterComponentBackground {
    position: absolute;
    height: 100%;
    width: 100%;
}

.masterComponentLogoutButton {
    position: absolute;
    right:13px;
    top:30%;
    color:teal; 
}

.masterComponentMenuButton {
    position: fixed;
    top:0;
    left: 0;
    z-index: 997;
    height:56px;
    color: white;
}

@media only screen and (min-height: 824px) {
    .masterComponentMenuButton {
        height: 64px;
    }
}

.navbar {
    height:56px;
}

@media only screen and (min-height: 824px) {
    .navbar {
        height: 64px;
    }
}

.navbarImage {
    position: absolute;
    height: 56px;
    width: 56px;
}

@media only screen and (min-height: 824px) {
    .navbarImage {
        position: absolute;
        height: 64px;
        width: 64px;
    }
}

.masterComponentSideBar {
    position: fixed;
    height: 100%;
}

.masterComponentSideBarItem {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.masterComponentLogoutButton a{
    color : teal
}

.logo{
    padding-left: 40%;
}

.logo a{
    width: 0;
    padding: 0 0 ;
}

.masterComponentMenuButton i{
    color : teal;
}

@media only screen and (max-width: 483px) {
    .splitModalButtons button{
        margin-top: 2%;
        width: -webkit-fill-available;
        width: -moz-available;
        width: stretch;
        float: right;
        margin-bottom: 3%;
    }
}


.splitModalButtons button{
    margin-right: 20px;
    margin-top: 2%;
    float: right;
    margin-bottom: 3%;
}

.row.splitModalButtons {
    margin-right: 20px;
    margin-top: 2%;
}
