    position: relative;
    height: 100%;
    width: 100%;
    display: inline-block;
}
.logo-wrapper .navbarImage{
    position: absolute;
    left: 50%;
    -webkit-transform: translateX(-50%);
        -ms-transform: translateX(-50%);
            transform: translateX(-50%);
}


